import wLoading from "@/components/w-loading/w-loading.vue";
import wPagination from "@/components/w-pagination/w-pagination.vue";
import qs from 'qs'
export default {
	data() {
		return {
			order_info: null,
			headConl: false,
			list: null,
			order_sn: null,
			current: 1,
			total: 0,
			count: 0
		}
	},
	components: {
		wLoading,
		wPagination
	},
	onLoad(options) {
		this.total = options.name_total
		this.order_sn = options.order_sn
		this.count = Math.ceil(this.total / 10)
		this.request(1);
		
	},
	methods: {
		detail(i) {
			let data = {
				app_mchid: uni.getStorageSync('token'),
				name_id: this.list[i].name_id,
				tk: this.list[i].tk,
				order_forms: 2,
				app: 1
			}
			uni.navigateTo({
				url: '../child_name/child_name?' + qs.stringify(data)
			});
		},
		nextPage(p) {
			this.current = p
			this.request(p)
			uni.pageScrollTo({
				scrollTop: 0
			})
		},
		prevPage(p) {
			this.current = p
			this.request(p)
			uni.pageScrollTo({
				scrollTop: 0
			})
		},
		request(p) {
			this.$nextTick(() => {
				this.$refs.loading.show();
			})

			uni.request({
				url: this.$store.state.baseUrl+'/api/child/order_detail',
				method: 'GET',
				data: {
					order_sn: this.order_sn,
					app: 1,
					p: p,
					ps: 10
				},
				success: (res) => {
					this.$refs.loading.close()
					if (res.data.code == 1){
						this.list = res.data.data.list
						this.order_info = res.data.data.order_info
					}else {
						uni.showModal({
							title: '提示',
							content: res.data.msg,
							success: function(res) {
								if (res.confirm) {
									uni.navigateBack()
								} else if (res.cancel) {
									uni.navigateBack()
								}
							}
						});
						return
					}
					
				}
			});
		}
	}
}
